*,
*::before,
*::after {
  box-sizing: border-box;
  text-decoration: none;
  text-transform: capitalize;
}

html {
  scroll-behavior: smooth;
  font-size: 12px;
}

html:focus-within {
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  background-color: #f2f2f2;
  margin: 0;
  padding: 0;
  line-height: 1.5;
  overflow-x: hidden;
  overflow-y: overlay;
}
body .Container {
  margin-inline: auto;
}

h1 {
  font-size: 4rem;
  font-weight: bolder;
  margin: 2rem 0;
  color: #f2f2f2;
}

p {
  font-size: 1.7rem;
  color: #f2f2f2;
  margin: 2rem 0;
  font-weight: 400;
}

button,
[button] {
  border: 2px solid #f2f2f2;
  width: 15rem;
  border-radius: 50px;
  background-color: transparent;
  font-weight: 400;
  font-size: 1.7rem;
  padding: 0.7rem 4rem;
  line-height: 1.5;
  white-space: nowrap;
  overflow: hidden;
  cursor: pointer;
  display: inline-flex;
  justify-content: center;
  text-align: center;
}

header {
  width: 100vw;
  height: 6rem;
  z-index: 1000;
}
header .Container {
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
header .Container .Logo a {
  cursor: pointer;
  font-size: 2rem;
  font-weight: 400;
  color: inherit;
}

section {
  width: 100vw;
  height: calc(100vh - 6rem);
}
section .Container {
  height: 100%;
}
section .Container .Hero {
  width: 100%;
  display: flex;
  align-items: center;
}
section .Container .Hero .box {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: blue;
  padding: 3rem;
}
section .Container .Hero .box img {
  width: 90px;
  height: 60px;
}
section .Container .Hero .box button {
  background-color: #f2f2f2;
}
section .Container .Hero .lift-box {
  background-color: #e38826;
}
section .Container .Hero .lift-box button {
  color: #e38826;
}
section .Container .Hero .lift-box button:hover {
  background-color: #e38826;
  color: white;
}
section .Container .Hero .midel-box {
  background-color: #006970;
}
section .Container .Hero .midel-box button {
  color: #006970;
}
section .Container .Hero .midel-box button:hover {
  background-color: #006970;
  color: white;
}
section .Container .Hero .right-box {
  background-color: #004241;
}
section .Container .Hero .right-box button {
  color: #004241;
}
section .Container .Hero .right-box button:hover {
  background-color: #004241;
  color: white;
}

footer {
  width: 100vw;
  font-size: 1.5rem;
  text-align: center;
}

.darkButton,
.lightButton {
  display: none;
}

.act {
  display: block;
  cursor: pointer;
  width: 40px;
  aspect-ratio: 1/1;
}

@media screen and (max-width: 2560px) {
  html {
    font-size: 18px;
  }

  .Container {
    width: 75%;
  }

  .Hero {
    height: calc(100vh - 6rem);
    flex-direction: row;
    margin: 0;
  }
  .Hero .box {
    width: 33.3333333333%;
    height: 80%;
  }
  .Hero .lift-box {
    border-radius: 20px 0 0 20px;
  }
  .Hero .right-box {
    border-radius: 0 20px 20px 0;
  }
}
/*For 4K */
@media screen and (max-width: 1600px) {
  html {
    font-size: 12px;
  }
}
/*For L Monitor */
@media screen and (max-width: 1024px) {
  html {
    font-size: 10px;
  }

  .Container {
    width: 80%;
  }
}
@media screen and (max-width: 768px) {
  .Hero {
    height: auto;
    flex-direction: column;
    margin: 5rem 0 10rem;
  }
  .Hero .box {
    width: 100%;
    height: 60vh;
  }
  .Hero .box p {
    max-width: 40ch;
  }
  .Hero .lift-box {
    border-radius: 20px 20px 0 0;
  }
  .Hero .right-box {
    border-radius: 0 0 20px 20px;
  }
}
/*For tablets */
@media (prefers-reduced-motion: reduce) {
  *,
::before,
::after {
    -webkit-animation-delay: -1ms !important;
            animation-delay: -1ms !important;
    -webkit-animation-duration: 1ms !important;
            animation-duration: 1ms !important;
    -webkit-animation-iteration-count: 1 !important;
            animation-iteration-count: 1 !important;
    background-attachment: initial !important;
    scroll-behavior: auto !important;
    transition-delay: 0s !important;
    transition-duration: 0s !important;
  }
}/*# sourceMappingURL=main-style.css.map */